<template>
	<div class="update_div">
		<div class="update_num">
			<span class="update_title">{{$t('storage.label_code')}}</span>
			<el-input v-model="label_code" clearable></el-input>
		</div>
		<div class="update_num">
			<span class="update_title">{{$t('storage.quantity')}}</span>
			<el-input v-model="quantity" clearable></el-input>
		</div>	
		<p class="update_null_tip" v-show="labelcodenull">*请输入唯一码（必填）</p>
		<p class="update_null_tip" v-show="quantitynull">*请输入唯一码和数量（必填）</p>
		<div class="update_btn">
			<el-button type="primary" @click="search">{{$t('common.search')}}</el-button>
			<el-button type="success" @click="refresh">{{$t('common.update')}}</el-button>
		</div>
		<v-detail ref="detail"></v-detail>
	</div>
</template>
<script>
import VDetail  from './detail'
import {updateMateQuantity} from '@/request/api/material'
	export default{
		components:{VDetail },
		data(){
			return{
				label_code:'',
				quantity:'',
				labelcodenull:false,
				quantitynull:false,
			}
		},
		methods:{
			search(){
				if (this.label_code) {
					this.labelcodenull = false
					this.quantitynull = false
					this.$refs.detail.showDetail(this.label_code)				
				} else {
					this.labelcodenull = true
				}				
			},
			refresh(){
				if (this.label_code && this.quantity) {
					this.labelcodenull = false
					this.quantitynull = false
					updateMateQuantity({label_code:this.label_code,quantity:this.quantity}).then(res =>{
						if (res.result) {
							this.$message({
								showClose:true,
								message:'更新成功',
								type:'success'
							})
						} else {
							this.$message({
								showClose:true,
								message:res.msg,
								type:'error'
							})
						}
					})
				} else {
					this.quantitynull = true
				}
								
			},
		}		
	}
</script>
<style>
.update_div{
	margin:60px;
	padding:40px;
	border:1px solid lightgray;
	text-align: left;
}
	.update_num{
		display: flex;
		height: 60px;
		line-height: 60px;
	}
	.update_num .el-input{
		width:240px;
	}
	.update_title{
		display: block;
		width:120px;
		font-size: 18px;
	}
	.update_btn{
		margin-top: 10px;
		margin-left: 120px;
	}
	.update_null_tip{
		color: red;
		height: 24px;
		line-height: 24px;
	}
</style>


