<template>
	<div>
		<el-dialog
			:title="title"
			:visible.sync="matedialog">
			<div class="matediv">
			<table class="mate_table">
				<tr class="label_tr">
          <td class="title_td">{{$t('storage.label_code')}}</td>
          <td class="content_td">{{mate_info.label_code}}</td>
          <td class="title_td">{{$t('storage.lot_code')}}</td>
          <td class="content_td">{{mate_info.lot_code}}</td>
        </tr>
        <tr>
          <td class="title_td">{{$t('storage.quantity')}}</td>
          <td class="content_td">{{mate_info.quantity}}</td>
          <td class="title_td">{{$t('storage.customer_name')}}</td>
          <td class="content_td">{{mate_info.customer}}</td>
        </tr>
        <tr>
          <td class="title_td">{{$t('storage.supplier_name')}}</td>
          <td class="content_td">{{mate_info.supplier_name}}</td>
          <td class="title_td">{{$t('storage.description')}}</td>
          <td class="content_td">{{mate_info.description}}</td>
        </tr>
        <tr>
          <td class="title_td">{{$t('storage.mfg_date')}}</td>
          <td class="content_td">{{mate_info.mfg_date}}</td>
          <td class="title_td">{{$t('storage.mate_type')}}</td>
          <td class="content_td">{{mate_info.mate_type}}</td>
        </tr>
        </table>
       </div>
		</el-dialog>

	</div>
</template>	
<script>
	import {searchByLabelCode} from '@/request/api/material'
	export default{
		data(){
			return{
				title:this.$t('storage.label_code'),
				matedialog:false,
				mate_info:{
					label_code:'',
					lot_code:'',
					quantity:'',
					customer:'',
					supplier_name:'',
					description:'',
					mfg_date:'',
					mate_type:''
				}
			}
		},
		methods:{
			showDetail(value){
				searchByLabelCode({label_code:value}).then(res =>{
					if (res.result) {
						if (Object.keys(res.mate_info).length) {
							this.matedialog = true
							this.title = this.$t('storage.label_code')+':'+value
							this.mate_info.label_code = res.mate_info.label_code
							this.mate_info.lot_code = res.mate_info.lot_code
							this.mate_info.quantity = res.mate_info.quantity
							this.mate_info.customer = res.mate_info.customer
							this.mate_info.supplier_name = res.mate_info.supplier_name
							this.mate_info.description = res.mate_info.description
							this.mate_info.mfg_date = res.mate_info.mfg_date
							this.mate_info.mate_type = res.mate_info.mate_type
						}else{
							this.$message({
								showClose:true,
								message:this.$t('common.nodata'),
								type:'warning'
							})
						}						
					} else {
						this.$message({
							showClose:true,
							message:res.msg,
							type:'error'
						})
					}
				})				
			}
		}
		
	}
</script>	
<style>
	.mate_table{
		border-collapse: collapse;
	  margin: 0 auto;
	  text-align: center;
	}
	.mate_table td{
		border:1px solid lightgray;
		width:200px;
		height: 40px;
	}
</style>


